body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Baloo 2', cursive;
    color: #333C5D;
    overflow-x: hidden;
}

/* a{
  text-decoration: none;
} */

hr {
    border: none;
    border: 1px solid #ddd;
}

h1 {
    font-size: 3rem;
    font-weight: bold;
}

h2 {
    font-size: 2.5rem;
    font-weight: bold;
}

h3 {
    font-size: 2rem;
    font-weight: bold;
}

h4 {
    font-size: 1.5rem;
    font-weight: normal;
}

h5 {
    font-size: 1.2rem;
    font-weight: normal;
}

h6 {
    font-size: 0.8rem;
    font-weight: normal;
}

/********** class selectors ************/
.container-fluid {
    width: 100%;
}

.large {
    width: 100%;
    border-radius: 0 0 0 0;
}

.small {
    width: 0px;
    display: none;
    border-radius: 0 0 0 0;
}

@media (max-width: 1080px) {
    .large {
        width: 0%;
        display: none;
        border-radius: 0 0 0 0;
    }

    .small {
        width: 100%;
        display: block;
        border-radius: 0 0 0 0;
    }
}

.mybutton {
    background: #221B1B;
    position: relative;
    border: none;
    color: white;
    padding: 7px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 0.9rem;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    /* border-radius: 8px; */
}

.mybutton lottie-player {
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    width: 100%;
    height: 150%;
}

.button5 {
    background: #348DCB;
    color: #fff;
    text-decoration: none;
    border-radius: 50px;
    padding: 5px 20px;
}

.buttonTransparent {
    border: 1px solid #fff;
    background: none;
    border-radius: 20px;
    padding: 0 10px;
    font-size: 0.8rem;
    font-weight: 500;
}

.buttonTransparent:hover {
    background: #fff;
    color: #161d65;
}

.material-symbols-outlined {
    font-variation-settings:
        'FILL' 0,
        'wght' 400,
        'GRAD' 0,
        'opsz' 48
}

.heading {
    display: flex;
    align-items: center;
}

.heading span {
    font-size: 2.3rem;
    padding-right: 10px;
}

.heading h4 {
    position: relative;
    top: 5px;
    font-size: 2.1rem;
    font-weight: bold;
}

@media (max-width: 850px) {
    .heading h4 {
        font-size: 1.5rem;
    }
}

.blink {
    -webkit-animation: blink 1.4s linear infinite;
            animation: blink 1.4s linear infinite;
}

@-webkit-keyframes blink {

    0%,
    60% {
        opacity: 1;
    }

    30% {
        opacity: 0;
    }
}

@keyframes blink {

    0%,
    60% {
        opacity: 1;
    }

    30% {
        opacity: 0;
    }
}

.btn-loading lottie-player {
    width: 50%;
    -webkit-transform: translate(50%, -9px);
            transform: translate(50%, -9px);
}
.before-nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: #333C5D;
    /* background: linear-gradient(to right, #348DCB,#161d65); */
    font-family: 'Baloo 2', cursive;
    padding: 0 0 0 22px;
    height: 45px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

@media (max-width: 1080px) {
    .before-nav {
        display: none;
    }
}

.icons {
    word-spacing: 20px;
    display: flex;
    align-items: center;
}

.icons a {
    color: #fff;
    margin: 0 10px;
    text-decoration: none;
    width: 25px;
    height: 25px;
    border: 1px solid #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
}

.before-nav-content {
    text-align: right;
    display: flex;
    align-items: center;
}

.before-nav-content .before-nav-icons {
    display: flex;
    align-items: center;
    padding: 0 20px;
    position: relative;
}

.before-nav-content .before-nav-icons a {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    margin: 0 15px;
    padding: 2px 10px;
}

.before-nav-content .before-nav-icons a:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.nav-location {
    text-decoration: none;
    display: flex;
    align-items: center;
    color: #fff;
    /* background: rgba(255, 255, 255, 0.5); */
    padding: 0 10px;
    height: 100%;
}

.nav-location:hover {
    color: #fff;
}

.nav-location span {
    font-variation-settings:
        'FILL' 0,
        'wght' 400,
        'GRAD' 200,
        'opsz' 48
}

.nav-location small {
    padding-left: 5px;
    font-weight: 600;
}

.notification-dot {
    position: absolute;
    top: 2px;
    right: 9px;
    width: 11px;
    height: 11px;
    background: red;
    border-radius: 50%;
    border: 2px solid #333C5D;
}
/* navbar start */
.navbar {
    background-color: #333C5D;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    /* box-shadow: 0 0 10px 0 rgba(0,0,0,0.2); */
}

@media (max-width: 1080px) {
    .navbar {
        display: none;
    }
}

.navbar-brand {
    padding-left: 8px;
}

.nav-item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    margin: 0 10px;
    border-radius: 55px 0;
    transition: 0.5s;
}

.nav-link {
    position: relative;
    top: 2px;
    color: #fff;
    font-size: 0.9rem;
    text-align: center;
    font-weight: 900;
}

.nav-link:hover {
    color: #ddd;
}

.nav-link small {
    position: relative;
    top: -7px;
    font-size: 0.95rem;
    padding-left: 5px;
}

.nav-item.active {
    position: relative;
    background: #fff;
    /* background: rgba(52, 141, 203,0.2); */
    /* background: #348DCB; */
    /* transform: translateY(-7px); */
    border-radius: 8px;
}

.nav-item.active .nav-link {
    color: #333C5D;
}
.navbar-mobile {
    background: #333C5D;
    padding-top: 0px;
    /* background: linear-gradient(to right,#161d65, #348DCB); */
    height: 54px;
    /* box-shadow: 0 0 10px 0 rgba(0,0,0,0.1); */
    z-index: 100;
}

.navbar-mobile-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 3px 10px;
}

.navbar-mobile-menu .navbar-search-icon {
    font-size: 2.1rem;
    font-variation-settings:
        'wght' 250
}

.navbar-mobile-menu a {
    color: #fff;
    /* color: #fff;
  font-size: 1.3rem; */
    display: flex;
    /* background: red; */
    text-decoration: none;
}

.navbar-mobile-menu a span {
    color: #fff;
    /* color: #348DCB; */
    /* color: #fff; */
    font-size: 1.3rem;
    position: relative;
    top: 1px;
}

.navbar-mobile-menu a h6 {
    position: relative;
    top: 3px;
    padding-left: 3px;
    padding-right: 3px;
    color: #fff;
    font-size: 0.9rem
}

.navbar-mobile-menu .hamburger-icon {
    position: absolute;
    top: 14px;
}

.navbar-mobile-menu .user-icon {
    font-size: 1rem;
    position: absolute;
    top: 11px;
    right: 7px;
    border: 1px solid #fff;
    line-height: 20px;
    text-align: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.notification-dot {
    position: absolute;
    width: 12px;
    height: 12px;
    background: red;
    border-radius: 50%;
    right: 0;
    border: 2px solid #333C5D;
}
/* bardown */
.navbar-mobile-bottom {
    position: fixed;
    background: #333C5D;
    /* background: linear-gradient(to right,#161d65, #348DCB); */
    /* background: #f4f4f4; */
    bottom: 0;
    width: 100%;
    z-index: 999;
    padding: 9px 0px 4px 0px;
    border-top: 0.01rem solid #aaa;
}

.navbar-mobile-bottom-menu {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar-mobile-bottom-menu .navbar-mobile-bottom-menu-a {
    color: #aaa;
    font-size: 0.9rem;
    text-align: center;
    text-decoration: none;
    width: 100%;
}

.navbar-mobile-bottom-menu .navbar-mobile-bottom-menu-a span {
    font-size: 1.3rem;
}

.navbar-mobile-bottom-menu a:nth-child(3) {
    width: 50%;
}

.navbar-mobile-bottom-menu .navbar-mobile-bottom-menu-a.active {
    color: #fff;
}

.navbar-mobile-bottom-menu .navbar-mobile-bottom-menu-a.active span {
    font-variation-settings:
        'FILL' 1,
        'wght' 400,
        'GRAD' 0,
        'opsz' 48
}

.new-user {
    position: absolute;
    z-index: 999;
    width: 40px;
    height: 40px;
    background: #348DCB;
    border-radius: 50%;
    bottom: 60%;
    left: calc(50% - 20px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 0.01rem solid #ddd;
    border-right: 0.01rem solid #ddd;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    text-decoration: none;
}

.new-user span {
    color: #fff;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.event-display-card {
    position: relative;
    /* height: 400px; */
    border-radius: 4px;
    overflow: hidden;
}

.event-display-card .content {
    position: relative;
    background: linear-gradient(to right, #2d2b4c 90%, rgba(0, 0, 0, 0));
    color: #fff;
    height: 100%;
    width: 120%;
    padding: 60px 80px 60px 40px;
    z-index: 5;
}

.event-display-card .content h1 {
    font-size: 1.6rem;
    font-weight: 600;
    letter-spacing: 1px;
}

.event-display-card .content h2 {
    font-size: 1rem;
    color: #aaa;
    font-weight: 500;
    padding-top: 10px;
}

.event-display-card .content h3 {
    font-size: 1.1rem;
    color: #ccc;
    font-weight: lighter;
    padding-bottom: 20px;
}

.event-display-card .content .rating small {
    padding-left: 5px;
    position: relative;
    top: -4px;
    font-size: 0.8rem;
}

.event-display-card .content .rating span {
    color: #fbba9c;
    font-size: 1.1rem;
}

.event-display-card .content .details,
.mobile-content .details {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 350px;
}

.event-display-card .content .details div,
.mobile-content .details div {
    text-align: center;
}

.event-display-card .content .details div h3,
.mobile-content .details div h3 {
    font-size: 1rem;
    color: #eee;
    margin: 0;
    padding-bottom: 0;
}

.event-display-card .content .details div h4,
.mobile-content .details div h4 {
    font-size: 0.7rem;
    color: #aaa;
}

.mobile-content .details div h4 {
    font-size: 0.9rem;
}

.mobile-content .event-location {
    position: absolute;
    width: 200px;
    background: red;
}

.mobile-content .event-location h1 {
    font-size: 1.3rem;
}

.event-display-card .content .button-div,
.mobile-content .button-div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    bottom: 40px;
    width: 100%;
    padding-right: 200px;
}

.mobile-content .button-div {
    position: relative;
    bottom: 0;
    padding: 0 20px 0 0;
}

.mobile-content .button-div h5 {
    font-size: 0.9rem;
}

.mobile-content .button-div .event-display-card .content button,
.mobile-content button {
    width: 110px;
    font-size: 1rem;
    height: 40px;
    text-transform: capitalize;
    background: #348DCB;
    color: #fff;
}

.event-display-card .content button span,
.mobile-content button span {
    font-size: 1.3rem;
    position: relative;
    top: -1px;
}

.event-display-card .content .button-div .circle,
.mobile-content .button-div .circle {
    position: relative;
    text-decoration: none;
    color: #fff;
    top: 5px;
}

.event-display-card .content .button-div .circle h1,
.mobile-content .button-div .circle h1 {
    font-size: 1.2rem;
    letter-spacing: 3px;
}

.mobile-content .button-div .circle h1 {
    font-size: 0.8rem;
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
    padding-left: 20px;
}

.event-display-card .content .button-div .circle div,
.mobile-content .button-div .circle div {
    position: absolute;
    top: 50%;
    left: -20px;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    width: 10px;
    height: 10px;
    background: #ff5757;
    border-radius: 50%;
}

.mobile-content .button-div .circle div {
    -webkit-transform: translateY(-17px) translateX(-35px);
            transform: translateY(-17px) translateX(-35px);
}

.event-display-card .content .button-div .circle div::before,
.mobile-content .button-div .circle div::before {
    content: '';
    position: absolute;
    top: -4px;
    left: -4px;
    width: 18px;
    height: 18px;
    border: 2px solid #ff5757;
    border-radius: 50%;
}

.mobile-content {
    display: none;
    position: relative;
    top: -30px;
    z-index: 999;
    background: linear-gradient(to top, #2d2b4c 70%, rgba(0, 0, 0, 0));
    width: 100%;
    padding: 40px 10px 10px 10px;
    border-radius: 0 0 4px 4px;
}

.mobile-content .mobile-content-below-div {
    position: absolute;
    bottom: 10px;
    width: 100%;
}

@media (max-width: 992px) {
    .mobile-content {
        display: block;
